import React, { useEffect, useState } from "react";
import { Carousel } from "antd";
import * as Realm from "realm-web";

import { Separator, VideoSlide } from "components/common";

const overVideoText = {
  color: "white",
};

const contentStyle = {
  height: "60vh",
  display: "flex",
  alignItems: "flex-end",
  justifyContent: "left",
  padding: 50,
  background: "#364d79",
  backgroundSize: "cover",
  backgroundPosition: "center",
};

const CarouselNews = ({ news }) => {
  const backgroundCover = {
    background:
      'linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.6)), url("/img/covers/' +
      news.content.cover +
      '")',
  };
  return (
    <div>
      <div style={{ ...contentStyle, ...backgroundCover }}>
        <div style={{ width: "80%" }}>
          <h5 style={{ color: "rgb(240, 240, 240)" }}>{news.title}</h5>
          <p style={{ color: "rgb(240, 240, 240)" }}>{news.content.text}</p>
        </div>
      </div>
    </div>
  );
};

const Home = () => {
  const [news, setNews] = useState([]);

  useEffect(() => {
    const app = new Realm.App({ id: "web-app-myllw" });
    const credentials = Realm.Credentials.apiKey(
      "9NCYWjxeuT26kYKHdB8zEzDEAr6z6nBw2BBplVdbWYnpibHip7zMgupLh2M68wuf"
    );

    app.logIn(credentials).then((user) => {
      user.functions.getAllNews().then((allNews) => {
        //console.log(news);
        setNews(allNews);
      });
    });
  }, []);

  return (
    <>
      {/*<VideoSlide src="mJ8gRxjFbWY" youtube first>
        <h3 style={overVideoText}>Welcome to</h3>
        <h1 style={overVideoText}>Weird Frames</h1>
      </VideoSlide>*/}
      <VideoSlide src="/video/show_reel.mp4" first>
        <h3 style={overVideoText}>Welcome to</h3>
        <Separator height={2} />
        <img
          src="/img/logos/weird_frames.png"
          alt="logo"
          style={{ maxWidth: "min(80vw, 400px)" }}
        />
      </VideoSlide>
      {news && (
        <div className="slide">
          <div className="container">
            <div>
              <h1>What's New</h1>
              <Separator height={2} />
              <Carousel autoplay>
                {news &&
                  news.map((el, idx) => {
                    return <CarouselNews key={idx} news={el} />;
                  })}
              </Carousel>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Home;
