import React from "react";
import { Row, Col } from "antd";

const TwoSidedBlock = ({
    title,
    description,
    imageSrc,
    alignment = "left",
  }) => {
    const error = '`alignment` must be "left" or "right"!'
    
    if (!["left", "right"].includes(alignment))
      throw error;
  
    let blockOrder = alignment === "right" ? [0, 1] : [1, 0];
  
    const imageGradient = {
      maskImage:
        "linear-gradient(to " +
        alignment +
        ", rgba(0,0,0,0) 0%,rgba(0,0,0,0.95) 60%)",
        WebkitMaskImage:
        "linear-gradient(to " +
        alignment +
        ", rgba(0,0,0,0) 0%,rgba(0,0,0,0.95) 60%)",
    };
  
    return (
      <Row>
        <Col
          md={{ order: 0 }}
          lg={{ span: 10, order: blockOrder[0] }}
          style={{ display: "flex", alignItems: "center" }}
        >
          <div>
            <h3 style={{ textTransform: "uppercase" }}>{title}</h3>
            <p>{description}</p>
          </div>
        </Col>
        <Col md={{ order: 1 }} lg={{ span: 14, order: blockOrder[1] }}>
          <img
            src={imageSrc}
            alt="logo"
            style={{ maxWidth: "100%", ...imageGradient }}
            className={"rouded-corners"}
          />
        </Col>
      </Row>
    );
  };

  export default TwoSidedBlock;